import Floor from './components/Floor/index.vue'
import Card from './components/Card/index.vue'
import {mapGetters} from 'vuex'
export default {
	components:{
		Floor,
		Card
	},
	data(){
		return {
			currentId:-1
		}
	},
	mounted(){
		this.getIndexData()
	},
	methods:{
		getIndexData(){
			this.$store.dispatch('getIndexData')
		}
	},
	computed:{
		...mapGetters(['navList','iconList','floorList'])
	}
}
